<template>
  <div class="chat-container">
    <div class="chat-header">
      <h2>WeChat-like Chat</h2>
      <div class="character-select">
        <button @click="toggleCharacterMenu" class="menu-button">
          {{ selectedCharacter ? selectedCharacter.split('.')[0] : 'ÈÄâÊã©ËßíËâ≤' }}
          <span class="dropdown-arrow">‚ñº</span>
        </button>
        <ul v-if="showCharacterMenu" class="character-list">
          <li v-for="charFile in characterFiles" :key="charFile" @click="selectCharacter(charFile)">
            {{ charFile.split('.')[0] }}
          </li>
        </ul>
      </div>
    </div>
    <div class="chat-messages">
      <ChatMessage v-for="(msg, index) in messages" :key="index" :message="msg" />
    </div>

    <div class="chat-input">
      <button @click="toggleListening" :disabled="isWaitingForResponse" class="mic-button">
        {{ isListening ? 'ÂÅúÊ≠¢' : 'üé§' }}
      </button>
      <input type="text" v-model="newMessage" @keydown.enter="sendMessage" placeholder="Type your message...">
      <button @click="sendMessage" class="send-button">Send</button>
    </div>
  </div>
</template>

<script>
import ChatMessage from './components/ChatMessage.vue';

export default {
  components: {
    ChatMessage,
  },
  data() {
    return {
      messages: [],
      newMessage: '',
      recognition: null,
      isListening: false,
      characterFiles: [],
      selectedCharacter: null,
      showCharacterMenu: false,
      firstMessage: true,
      isWaitingForResponse: false,
      lastSpeechTimestamp: 0,
      silenceTimer: null,
      interimTranscript: '', // Êñ∞Â¢ûÔºö‰øùÂ≠ò‰∏≠Èó¥ÁªìÊûú
    };
  },
  mounted() {
    this.fetchCharacterFiles();

    if ('SpeechRecognition' in window || 'webkitSpeechRecognition' in window) {
      this.recognition = new (window.SpeechRecognition || window.webkitSpeechRecognition)();
      this.recognition.lang = 'zh-CN';
      this.recognition.continuous = true;
      this.recognition.interimResults = true;

      this.recognition.onresult = (event) => {
        if (this.isWaitingForResponse) return;

        let finalTranscript = '';
        this.interimTranscript = ''; // ÈáçÁΩÆ‰∏≠Èó¥ÁªìÊûú
        for (let i = event.resultIndex; i < event.results.length; ++i) {
          if (event.results[i].isFinal) {
            finalTranscript += event.results[i][0].transcript;
            this.lastSpeechTimestamp = Date.now();
          } else {
            this.interimTranscript += event.results[i][0].transcript; // Á¥ØÁßØ‰∏≠Èó¥ÁªìÊûú
          }
        }

        if (finalTranscript) {
          this.newMessage = finalTranscript;
          this.resetSilenceTimer();
        } else if (this.interimTranscript) {
            this.newMessage = this.interimTranscript; //ÂÆûÊó∂ÊòæÁ§∫
        }
      };

      this.recognition.onend = () => {
        if (this.isListening) {
          this.recognition.start();
        }
      };

      this.recognition.onerror = (event) => {
        console.error('Speech recognition error:', event.error);
        this.isListening = false;
        this.isWaitingForResponse = false;

        if (event.error === 'no-speech') {
          alert('Ê≤°ÊúâÊ£ÄÊµãÂà∞Â£∞Èü≥ÔºåËØ∑ËØ¥ËØù„ÄÇ');
        } else if (event.error === 'audio-capture') {
          alert('Êó†Ê≥ïËÆøÈóÆÈ∫¶ÂÖãÈ£éÔºåËØ∑Ê£ÄÊü•È∫¶ÂÖãÈ£éÊòØÂê¶Â∑≤ËøûÊé•„ÄÇ');
        } else if (event.error === 'not-allowed') {
          // Êõ¥ÊòéÁ°ÆÁöÑÊùÉÈôêÊèêÁ§∫
          alert('Êú™ÊéàÊùÉÈ∫¶ÂÖãÈ£éËÆøÈóÆÔºåËØ∑Âú®ÊµèËßàÂô®ËÆæÁΩÆ‰∏≠ÂÖÅËÆ∏Ê≠§ÁΩëÁ´ô‰ΩøÁî®È∫¶ÂÖãÈ£é„ÄÇ');
        } else {
          alert('ËØ≠Èü≥ËØÜÂà´Âá∫Èîô: ' + event.error);
        }
      };
    } else {
      alert('ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅËØ≠Èü≥ËØÜÂà´„ÄÇ');
    }
  },
  methods: {
    fetchCharacterFiles() {
      fetch('http://127.0.0.1:5000/list_characters')
        .then(response => response.json())
        .then(data => {
          if (data.characters) {
            this.characterFiles = data.characters;
          }
        })
        .catch(error => console.error('Error fetching characters:', error));
    },
    selectCharacter(charFile) {
      this.selectedCharacter = charFile;
      this.showCharacterMenu = false;
      this.firstMessage = true;
    },
    toggleCharacterMenu() {
      this.showCharacterMenu = !this.showCharacterMenu;
    },
    async sendMessage() {
      if (!this.newMessage.trim()) return;

      const requestData = {
        message: this.newMessage,
        character: this.selectedCharacter,
        first_message: this.firstMessage,
      };

      this.messages.push({ text: this.newMessage, type: 'user' });
      this.newMessage = '';  // ËøôÈáå‰∏çÊ∏ÖÁ©∫ÔºåsendMessage ÂêéÁî±ÈùôÈü≥Ê£ÄÊµãÂÜ≥ÂÆöÊòØÂê¶ÂèëÈÄÅ
      this.interimTranscript = ''; // Ê∏ÖÁ©∫‰∏≠Èó¥ÁªìÊûú
      this.isWaitingForResponse = true;

      const aiMessageIndex = this.messages.push({ text: '', type: 'ai', loading: true }) - 1;

      try {
        const response = await fetch('http://127.0.0.1:5000/chat', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(requestData),
        });

        const data = await response.json();

        if (data.response) {
          this.messages[aiMessageIndex].text = data.response;
          this.messages[aiMessageIndex].loading = false;
        } else {
          this.messages[aiMessageIndex].text = 'Error: ' + (data.error || 'Unknown error');
          this.messages[aiMessageIndex].loading = false;
        }

        this.$nextTick(() => this.scrollToBottom());
        this.firstMessage = false;

      } catch (error) {
        console.error('Error:', error);
        this.messages.push({ text: 'Error: Could not connect to the server.', type: 'ai', loading: false });
        this.$nextTick(() => this.scrollToBottom());

      } finally {
        this.isWaitingForResponse = false;
        this.resetSilenceTimer();
      }
    },
    toggleListening() {
      if (this.isListening) {
        this.stopListening();
      } else {
        this.startListening();
      }
    },
    startListening() {
      if (!this.recognition) return;
      this.isListening = true;
      this.recognition.start();
      this.resetSilenceTimer();
    },
    stopListening() {
      this.isListening = false;
      this.recognition.stop();
      clearTimeout(this.silenceTimer);
    },
    resetSilenceTimer() {
      clearTimeout(this.silenceTimer);
      this.silenceTimer = setTimeout(() => {
        if (this.newMessage.trim() && !this.isWaitingForResponse) {
          this.sendMessage();
           this.newMessage = ''; // Âú®ËøôÈáåÊ∏ÖÁ©∫ÔºåÁ°Æ‰øùÂèëÈÄÅÂêéËæìÂÖ•Ê°ÜË¢´Ê∏ÖÁ©∫
        }
      }, 2000);
    },
    scrollToBottom() {
      const messagesContainer = this.$el.querySelector('.chat-messages');
      if (messagesContainer) {
        messagesContainer.scrollTop = messagesContainer.scrollHeight;
      }
    },
  },
};
</script>

