<script>
export default {
  props: {
    message: {
      type: Object,
      required: true,
    },
  },
  computed: {
    messageClass() {
      return {
        'user-message': this.message.type === 'user',
        'ai-message': this.message.type === 'ai',
      };
    },
  },
};
